<template v-slot:[slotname]="trigger">
    <div class="accordion" v-bind:class="theme">
        <div class="header" v-on:click="toggle">
            <slot name="header">Header</slot>
            <i class="fa fa-2x fa-angle-down header-icon" v-bind:class="{ rotate: show }"></i>
        </div>
        <transition name="accordion"
        v-on:before-enter="beforeEnter" v-on:enter="enter"
        v-on:before-leave="beforeLeave" v-on:leave="leave">
        <div class="body" v-show="show">
            <div class="body-inner">
                <slot name="body">Body</slot>
            </div>
        </div>
        </transition>
    </div>   
</template>
<script>
    export default {
        name: "Accordion",
        data: () => ({
            show: false
        }),
        methods: {
            toggle: function() {
            this.show = !this.show;
            },
            // enter: function(el, done) {
            //   $(el).slideDown(150, done);
            // },
            // leave: function(el, done) {
            //   $(el).slideUp(150, done);
            // },
            beforeEnter: function(el) {
            el.style.height = '0';
            },
            enter: function(el) {
            el.style.height = (el.scrollHeight/13) + 'em';
            },
            beforeLeave: function(el) {
            el.style.height = (el.scrollHeight/13) + 'em';
            },
            leave: function(el) {
            el.style.height = '0';
            }
        }
    }  
</script>
<style scoped>
    .accordion {
        margin-bottom: 1em;
    }
    .accordion:hover {
        background-color: #0fff931f;        
    }

    .accordion .header {
        height: 3em;
        line-height: 3em;
        cursor: pointer;
        display: flex;
        border: 1px solid #090d1ed9;
        padding: 0 2em; 
    }

    .accordion .header-icon {
    position: absolute;
    top: 5px;
    right: 8px;
    transform: rotate(0deg);
    transition-duration: 0.3s;
    }

    .accordion .body {
    /*   display: none; */
    display: flex;
    border: 1px solid #090d1ed9;
    overflow: hidden;
    background-color: #fff;
    border-top: 0;
    transition: 150ms ease-out;
    padding: 0 2em;
    line-height: 1.5em;
    }

    .accordion .body-inner {
    overflow-wrap: break-word;
    /*   white-space: pre-wrap; */
    }

    .accordion .header-icon.rotate {
    transform: rotate(180deg);
    transition-duration: 0.3s;
    }

    .accordion.main {
    }

    .accordion.main .body {
    border-color: #8c618d;
    }
</style>